define('dummy/controllers/dashboard', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    sortBy: 'username',
    sortDirection: 'asc',
    localStorageService: {
      set: function set(key, value) {
        localStorage.setItem(key, JSON.stringify(value));
      },
      remove: function remove(key) {
        localStorage.removeItem(key);
      },
      get: function get(key) {
        var value = localStorage.getItem(key);
        if (value) {
          return JSON.parse(value);
        }
        return false;
      },
      flush: function flush() {
        localStorage.clear();
      }
    },

    init: function init() {
      this._super.apply(this, arguments);
      this.findUsers(1);
    },
    actions: {
      paginate: function paginate(page) {
        this.findUsers(page);
      },
      sort: function sort(field, direction) {
        this.sortBy = field;
        this.sortDirection = direction;
        this.findUsers(1);
      }
    },

    findUsers: function findUsers(page) {
      var _this = this;

      this.store.query('user', { page: page, sort_by: this.sortBy, sort_direction: this.sortDirection }).then(function (users) {
        _this.set('users', users);
      }, function (reason) {
        console.info(reason, 'Mirage could not fetch the users data!');
      });
    },

    columns: [{
      id: 'username',
      name: 'Username',
      sortable: true,
      sort: 'username',
      sortAsc: true,
      visible: true,
      width: 14,
      value: function value(o) {
        return o.get('username');
      }
    }, {
      id: 'email',
      name: 'Email',
      sortable: true,
      sort: 'email',
      visible: true,
      width: 20,
      value: function value(o) {
        return o.get('email');
      }
    }, {
      id: 'name',
      name: 'Full name',
      sortable: true,
      sort: 'name',
      visible: true,
      width: 33,
      value: function value(o) {
        return o.get('name');
      }
    }, {
      id: 'status',
      name: 'Status',
      sortable: true,
      sort: 'status',
      visible: true,
      width: 18,
      value: function value(o) {
        return o.get('status');
      }
    }, {
      id: 'actions',
      name: 'Actions',
      sortable: false,
      sort: '',
      visible: true,
      width: 15,
      value: function value(o) {
        return {
          template: 'actions-row',
          value: o
        };
      }
    }]
  });

});